import Vue from 'vue'
import VueRouter from 'vue-router'
import Config from './../pages/Config.vue'
Vue.use(VueRouter)

const routes = [{
    path: '/config',
    name: 'Config',
    component: Config
}]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router