import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        clients: []
    },
    mutations: {
        clients(state, data) {
            let newClients = []
            data.forEach(client => {
                newClients.push({
                    name: client,
                    time: new Date(Date.now()).toLocaleTimeString(),
                    status: 'idle',
                    message: 'Подключился!'
                })
            })
            state.clients = newClients
        }
    },
    actions: {
        clientsAct({ commit }, data) {
            commit('clients', data)
        }
    },
    modules: {},
    getters: {
        getClients: state => state.clients
    }
})